/* Other Multiple Choice Elements */

._pi-control {
  display: block;
  position: relative;
  /* 		margin:		0 0 0 30px; -- DEFINED IN FLEXX -- */
}

._pi-control input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

._pi-control-indicator {
  position: absolute;
  border: 1px solid black;
  /*
		top:		7px; -- DEFINED IN FLEX --
		left:		-25px;
		height:		15px;
		width:		15px;
*/
}

._pi-control-indicator:after {
  content: '';
  position: absolute;
  display: none;
}

._pi-control input:checked~._pi-control-indicator:after {
  display: block;
}

._pi-control-checkbox ._pi-control-indicator:after {
  /*
		left:			4px; -- DEFINED IN FLEX --
		width:			5px;
		height:			11px;
		border-width:	0 2px 2px 0;
*/
  border: solid #000;
  transform: rotate(45deg);
}

ul._pi_answers_container li label input._pi_checkbox {
  margin-right: 10px;
  cursor: pointer;
}

div._pi_multiple_choices_count {
  text-align: center;
}

